import axios from 'axios';
import QueryString from 'qs';

export var OPAPI ={
    singlePre: function (params, callback) {
        var config = {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
            responseType: 'json'
        };

        const data = QueryString.stringify({
            opname: params.opname,
            diagnosis: params.diagnosis,
            divcode: params.divcode,
            threshold : params.threshold,
            prediction_type : params.prediction_type
            // opname: 'INSERTION TENKHOFF PERITONEAL DIALYSIS CATH. Chronic renal failure',
            // diagnosis: 'INSERTION',
            // divcode: 'ACS',
            // threshold : '0.3',
            // prediction_type : 'single'
        });

        axios.post('http://210.115.46.198:8080/predictions/ICD', data, config)
        .then(response => callback(null, response.data))
        .catch(err => callback(err))
    },

    multiPre: function (params, callback) {
        var config = {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
            responseType: 'json'
        };

        const data = QueryString.stringify({
            data: params.data,
            threshold : params.threshold,
            prediction_type : params.prediction_type
        });

        axios.post('http://210.115.46.198:8080/predictions/ICD', data, config)
        .then(response => callback(null, response.data))
        .catch(err => callback(err))
    }
}