<template>
  <div>
    <div class="top_menu">
      <nav class="navbar navbar-expand-lg navbar-light bg-light">
        <div class="container-fluid">
          <a class="navbar-brand" href="#" id="title" @click="initPage('single')">Opname to ICD-code</a>
          <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
          </button>
          <div class="collapse navbar-collapse" id="navbarNav">
            <ul class="navbar-nav">
              <li class="nav-item">
                <a :class="['nav-link', isSingle ? 'active' : '']" href="#" @click="initPage('single')">Single prediction</a>
              </li>
              <li class="nav-item">
                <a :class="['nav-link', isMulti ? 'active' : '']" href="#" @click="initPage('multi')">File prediction (multi)</a>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </div>

    <!-- single -->
    <div class="single" v-if="showTab=='single'">
      <div class="singleWrapper">
        <div class="box">
          <h3>Department Code</h3>
          <select v-model="singleParams.divcode">
            <option value="">None</option>
            <option>ACS</option>
            <option>BES</option>
            <option>CRS</option>
            <option>GES</option>
            <option>HPS</option>
            <option>PDS</option>
            <option>TVS</option>
          </select>
        </div>

        <div class="box">
          <h3>threshold</h3>
          <input type="number" min="0.0" max="1" step="0.1" v-model="singleParams.threshold">
          <p class="tip">Only codes above the input value are recommended. (enter only values between 0.0~1.0)</p>
        </div>

        <div class="box">
          <h3>Operation Name</h3>
          <input type="text" v-model="singleParams['opname']" placeholder="type your operation name">
        </div>

        <div class="box">
          <h3>Diagnosis</h3>
          <input type="text" v-model="singleParams['diagnosis']" placeholder="">
        </div>

        <button @click="reqSingle" class="btn_pre">prediction</button>
        <button @click="makeExcelFile" class="btn_down">download</button>

        <table id="singleTable">
          <thead>
            <tr>
              <th>ID</th>
              <th>Operation Name</th>
              <th width="20%">ICD9-code</th>
              <th width="20%">Prob</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(a, i) in singleRes" :key="a">
              <td>{{i*1+1}}</td>
              <td>{{singleRes[i][0]}}</td>
              <td><text v-for="(b, j) in singleRes[i][1]" :key="b"><text v-if="j!=0"> / </text>{{singleRes[i][1][j]}}</text></td>
              <td><text v-for="(b, j) in singleRes[i][2]" :key="b"><text v-if="j!=0"> / </text>{{singleRes[i][2][j]}}</text></td>
            </tr>
          </tbody>
        </table>
        
      </div>
    </div>

    <!-- multi -->
    <div class="multi" v-else>
      <div class="box">
        <h3>threshold</h3>
        <input type="number" min="0.0" max="1" step="0.1" v-model="multiParams.threshold">
        <p class="tip">Only codes above the input value are recommended. (enter only values between 0.0~1.0)</p>
      </div>

      <div class="box">
        <h3>File Upload</h3>
        <input type="file" style="display: none;" ref="doc" @change="this.fileState = this.$refs.doc.files[0].name" />
        <div class="fileWrapper">
          <button id="btnUploadFile" @click="this.$refs.doc.click()">File</button>
          <p style="display: inline-block; margin: 0 0 0 10px;">{{fileState}}</p>
        </div>
        <p class="tip">Only .csv files can be uploaded.</p>
      </div>

      <button @click="readFile" class="btn_pre">prediction</button>
      <button @click="makeExcelFile" class="btn_down">download</button>

      <div class="resultWrapper">
        <table id="multiTable">
          <thead>
            <tr>
              <th>ID</th>
              <th>Operation Name</th>
              <th width="25%">ICD9-code</th>
              <th width="25%">Prob</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(a, i) in multiRes" :key="a">
              <td>{{i*1+1}}</td>
              <td>{{multiRes[i][0]}}</td>
              <td><text v-for="(b, j) in multiRes[i][1]" :key="b"><text v-if="j!=0"> / </text>{{multiRes[i][1][j]}}</text></td>
              <td><text v-for="(b, j) in multiRes[i][2]" :key="b"><text v-if="j!=0"> / </text>{{multiRes[i][2][j]}}</text></td>
            </tr>
          </tbody>
        </table>
      </div>
      
    </div>
  </div>
</template>

<script>
import {OPAPI} from "@/op-api.js";
// import XLSX from 'xlsx'
export default {
  name: 'App',
  data() {
    return {
      isSingle: true,
      isMulti: false,
      showTab: 'single',
      divcode: '',
      singleParams: {divcode: '', threshold: '0.5', prediction_type: 'single'},
      multiParams: {threshold: '0.5', prediction_type: 'multi'},
      singleRes: {},
      multiRes: {},
      file: null,
      fileState: "No files selected."
    }
  },
  components: {
  },
  methods: {
    initPage(page) {
      if(page == 'single') {
        this.isSingle = true;
        this.isMulti = false;
      } else {
        this.isSingle = false;
        this.isMulti = true;
      }
      this.showTab = page;
      this.divcode = '';
      this.singleParams = {divcode: '', threshold: '0.5', prediction_type: 'single'};
      this.multiParams = {threshold: '0.5', prediction_type: 'multi'};
      this.singleRes = {};
      this.multiRes = {};
    },
    reqSingle() {
      if(this.singleParams.opname == '' || this.singleParams.opname == undefined) {
        alert("Please enter the Operation Name.");
      } else {
        OPAPI.singlePre(this.singleParams, function(err,res) { 
          this.singleRes = res;
        }.bind(this));
      }
    },
    reqMulti() {
      OPAPI.multiPre(this.multiParams, function(err,res) { 
        this.multiRes = res;
      }.bind(this));
     
    },
    readFile() {
      this.file = this.$refs.doc.files[0];
      const reader = new FileReader();

      if((this.file != undefined)) {
        if (this.file.name.includes(".csv")) {
          this.fileState = this.file.name;
          reader.onload = (res) => {
            this.multiParams["data"] = res.target.result;
            this.reqMulti();
          };
          reader.onerror = (err) => console.log(err);
          reader.readAsText(this.file);
        } else {
          alert('Only .csv files can be uploaded.');
        }
      } else {
        alert('Please upload a file.');
      }
    },
    makeExcelFile() {
      let tab_text = '<html xmlns:x="urn:schemas-microsoft-com:office:excel">'
          tab_text += '<head><meta http-equiv="content-type" content="application/vnd.ms-excel; charset=UTF-8">'
          tab_text += '<xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet>'
          tab_text += '<x:Name>Test Sheet</x:Name>'
          tab_text += '<x:WorksheetOptions><x:Panes></x:Panes></x:WorksheetOptions></x:ExcelWorksheet>'
          tab_text += '</x:ExcelWorksheets></x:ExcelWorkbook></xml></head><body>'
          tab_text += "<table>"
      const temp = document.getElementById(this.showTab+'Table').innerHTML
      tab_text += temp
      tab_text += '</table></body></html>'
      const fileName = 'result.xlsx'
      const a_tag = document.createElement('a')
      const blob = new Blob([tab_text], { type: 'application/vnd.ms-excel;charset=utf-8;' })
      a_tag.href = window.URL.createObjectURL(blob)
      a_tag.download = fileName
      a_tag.click()
    }
  }
}
</script>

